<template>
    <div class="detail-container">
        <div class="detail" :class="{ slideIn: visible }">
            <div class="main-content"></div>
        </div>
        <div class="mask" v-show="visible" v-tap="{ methods:closeDetail }"></div>
    </div>
</template>

<script>
export default {
    props: ['detail', 'visible'],
    data () {
        return {
            classInfo: {}
        }
    },
    methods: {
        closeDetail () {
            this.$emit('closer')
        }
    },
    watch: {
        detail () {
            this.classInfo = JSON.parse(window.localStorage.getItem(this.detail))
        }
    }
}
</script>

<style lang="less" scoped>
.detail-container {
    position: fixed;
    top: 0;
    left: 0;

    width: 0;
    height: 0;

    pointer-events: auto;

    .detail {
        position: fixed;
        bottom: 0;
        left: 0;

        width: 100%;
        height: 300px;

        padding: 10px;
        
        transform: translateY(100%);
        transition: transform 300ms cubic-bezier(0.33,-0.01, 0.35, 1.01);

        .main-content {
            width: 100%;
            height: 100%;

            background: #FFF;
            
            border-radius: 6px;
            box-shadow: 0 3px 10px rgba(0,0,0,.3);
        }
    }

    .slideIn {
        transform: translateY(0);
    }

    .mask {
        position: fixed;
        top: 0;
        left: 0;

        width: 100%;
        height: 100%;

        background: transparent;
    }
}
</style>



